import React, { useEffect, useMemo, useState } from 'react';
import _ from 'lodash';

import styles from './QuizResultOverall.module.scss';
import Badge from '../../UI/Badge/Badge';
import Icon from '../../UI/Icon/Icon';
import Tooltip from '../../UI/Tooltip/Tooltip';
import Help from '../../UI/Help/Help';
import { useContextSelector } from 'use-context-selector';
import { UIContext } from '../../context/UI/UIContext';
import Alert from '../../UI/Alert/Alert';
import Label from '../../context/Label/Label';
import useLabel from '../../context/Label/useLabel';

function QuizResultOverall({
  resourceId,
  globalState,
}: {
  resourceId: string;
  globalState: any;
}) {
  const { setCourseScore, setCoursePassed, setCourseFailed, onPageCompleted } =
    globalState;
  const startBlinkNext = useContextSelector(UIContext, (c) => c.startBlinkNext);
  const { plainLabel } = useLabel();
  const [expanded, setExpanded] = useState<boolean>(false);
  const [allQuizzesCompleted, setAllQuizzesCompleted] =
    useState<boolean>(false);

  const scores = useMemo(() => {
    // 1. filter all quizzes from this course
    const quizzes = _.filter(
      globalState?.course?.pages,
      (p) => p?.resourceType === 'quiz'
    );

    // 2. is there any quiz with computeOverallScore setted?
    const hasAnyQuizWithOverallConfig = !!_.find(
      quizzes,
      (q) => q?.quiz?.config?.computeOverallScore === true
    );

    // 3. filter quizzes with computeOverallScore OR the last quiz
    const quizzesToConsider = hasAnyQuizWithOverallConfig
      ? _.filter(quizzes, (q) => q?.quiz?.config?.computeOverallScore === true)
      : [quizzes[quizzes.length - 1]];

    // 4. return the data
    const _scores = _.map(quizzesToConsider, (quiz) => {
      const quizTracking = _.get(globalState?.tracking, quiz?.resourceId);

      return {
        resourceId: quiz?.resourceId,
        title: quiz?.title,
        score: quizTracking?.result?.score || 0,
        passingScore: quiz?.quiz?.config?.passingScore,
        completed: quizTracking?.completed,
      };
    });

    return _scores;
  }, [globalState]);

  const overallScore = useMemo(() => {
    let scoreSum = 0;
    let passingScoreSum = 0;
    let completedAll = true;

    _.forEach(scores, (s) => {
      scoreSum += +s.score;
      passingScoreSum += +s.passingScore;
      completedAll = s.completed && completedAll;
    });

    // to trigger the quiz completion status
    setAllQuizzesCompleted(completedAll);

    const score = Math.round(scoreSum / scores.length);
    const passingScore = Math.round(passingScoreSum / scores.length);

    return {
      score,
      passingScore,
      passed: score >= passingScore,
      completed: completedAll,
    };
  }, [scores]);

  const isThisQuizInOverallScore = !!_.find(
    scores,
    (s) => !!resourceId && s.resourceId === resourceId
  );

  useEffect(() => {
    // mark page as completed
    onPageCompleted(() => startBlinkNext());
  }, []); // eslint-disable-line

  useEffect(() => {
    // valid only is all quizzes (could be only one) are/is completed
    if (!allQuizzesCompleted) return;

    // set course score
    setCourseScore(+overallScore.score);

    // set passed or failed
    overallScore.passed ? setCoursePassed() : setCourseFailed();
  }, [allQuizzesCompleted]); // eslint-disable-line

  if (!isThisQuizInOverallScore) {
    return (
      <div className={styles.root} data-testid="QuizResultOverall">
        <Alert className={styles.alert}>
          <Label>
            This score will not be computed in your final overall score
          </Label>
        </Alert>
      </div>
    );
  }

  // if only one quiz is available to be graded, does not show this component on screen
  if (_.size(scores) <= 1) return null;

  return (
    <div className={styles.root} data-testid="QuizResultOverall">
      <span className={styles.title}>
        <Label>Overall score</Label>{' '}
        <Help
          tooltip={plainLabel(
            'It will be reported as your final score when you complete all graded quiz on this course.'
          )}
        />
      </span>
      <div className={styles.detail}>
        <Tooltip
          tooltip={
            overallScore.completed
              ? plainLabel(
                  'Based on all graded quizzes answered on this course.'
                )
              : plainLabel(
                  'Complete all graded quizzes on this course for the final overall score.'
                )
          }
          id={`overall`}
        >
          <Badge
            success={
              overallScore.completed &&
              overallScore.score >= overallScore.passingScore
            }
            danger={
              overallScore.completed &&
              overallScore.score < overallScore.passingScore
            }
            cancel={!overallScore.completed}
            className={[styles.badge, styles.badgeMain].join(' ')}
            onClick={() => setExpanded(!expanded)}
          >
            {overallScore.score}%
          </Badge>
        </Tooltip>
        <Icon
          icon={expanded ? 'chevron-left' : 'chevron-right'}
          onClick={() => setExpanded(!expanded)}
          className={styles.iconToggleExpand}
        />
        <div className={!expanded ? styles.hidden : ''}>
          {_.map(scores, ({ title, score, passingScore, completed }, i) => (
            <Tooltip
              tooltip={`${title}</br>(${plainLabel(
                'Passing score'
              )}: ${passingScore}%)`}
              bottom
              key={i}
              id={`quiz-${i}`}
            >
              <Badge
                success={completed && score >= passingScore}
                danger={completed && score < passingScore}
                cancel={!completed}
                className={styles.badge}
              >
                {completed ? `${score}%` : '-'}
              </Badge>
            </Tooltip>
          ))}
        </div>
      </div>
    </div>
  );
}

export default QuizResultOverall;
