import React from 'react';

import styles from './Header.module.scss';
import Icon from '../UI/Icon/Icon';

function Header({ globalState }) {
  const {
    toggleMenu,
    getColorScheme,
    course,
    exitCourse,
    showCloseCourseButton,
  } = globalState;
  const { primary } = getColorScheme();

  return (
    <div className={styles.Header} style={{ backgroundColor: primary }}>
      <div className={styles.Header__left}>
        <Icon
          icon="bars"
          onClick={toggleMenu}
          className={styles.Header__icon}
        />
        <span className={styles.Header__title}>{course?.name}</span>
      </div>
      <div className={styles.Header__right}>
        <div
          className={styles.Header__logo}
          style={{
            backgroundImage: course?.logo?.name
              ? `url('${course.baseURL}/${course?.logo?.name}')`
              : null,
          }}
        />
        {showCloseCourseButton && (
          <Icon
            icon="times"
            onClick={exitCourse}
            className={styles.Header__closeIcon}
            testId="close-course-button"
          />
        )}
      </div>
    </div>
  );
}

export default Header;
