import React, { useEffect, useRef, useState } from 'react';
import { useContextSelector } from 'use-context-selector';

import styles from './Main.module.scss';
import Slide from '../Content/Slide/Slide';
import Youtube from '../Content/Youtube/Youtube';
import Quiz from '../Content/Quiz/Quiz';
import Video from '../Content/Video/Video';
import Game from '../Content/Game/Game';
import { UIContext } from '../context/UI/UIContext';

function Main({ globalState }) {
  const { getCurrentPage, course } = globalState;
  const page = getCurrentPage();
  const [isChangingPage, setIsChangingPage] = useState(false);
  const timeout = useRef();
  const background = useContextSelector(UIContext, (c) => c.background);

  useEffect(() => {
    if (!isChangingPage) return;
    clearTimeout(timeout.current);
    timeout.current = setTimeout(() => setIsChangingPage(false), 50);
  }, [isChangingPage]);

  useEffect(() => {
    setIsChangingPage(true);
  }, [page.resourceId]);

  // onUnmount: clear timeout to avoid render errors
  useEffect(() => {
    return () => clearTimeout(timeout.current);
  }, []);

  function renderPage() {
    switch (page?.resourceType) {
      case 'slide':
        return (
          <Slide
            filename={page?.filename}
            baseURL={course.baseURL}
            hyperlink={page?.hyperlink}
            globalState={globalState}
          />
        );
      case 'video':
        if (page?.video?.type === 'youtube') {
          return (
            <Youtube
              url={page.video.youtubeUrl}
              settings={page?.settings}
              globalState={globalState}
            />
          );
        } else if (page?.video?.type === 'upload') {
          return (
            <Video
              filename={page?.video?.name}
              baseURL={course.baseURL}
              settings={page?.settings}
              globalState={globalState}
            />
          );
        } else {
          return null;
        }
      case 'quiz':
        return <Quiz {...page} globalState={globalState} />;
      case 'game':
        return <Game {...page} globalState={globalState} />;
      default:
        return <span>{page?.type}</span>;
    }
  }

  if (isChangingPage) return null;

  const backgroundOpacity =
    background?.opacity >= 0 && background?.opacity <= 1
      ? 1 - background.opacity
      : 0;

  return (
    <div
      className={styles.Main}
      style={{
        backgroundImage:
          background?.image?.name && course?.baseURL
            ? `linear-gradient(rgba(255, 255, 255, ${backgroundOpacity}), rgba(255, 255, 255, ${backgroundOpacity})), url('${course?.baseURL}/${background?.image?.name}')`
            : // example on how to darken a background-image (because opacity does not work)
              // `linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url('${course?.baseURL}/${background?.image?.name}')`
              undefined,
      }}
    >
      {renderPage()}
    </div>
  );
}

export default Main;
